* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul li a {
  color: #00f;
  text-shadow: 1px 0 1px rgba(236, 187, 200, .9);
  text-decoration: none;
}

.link {
  width: fit-content;
  color: #00f;
  background-color: #97a5ff;
  border-radius: 120px 100px 15px;
  margin: 10px;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  list-style-type: none;
  transition: background-color .3s ease-in-out;
  display: block;
  box-shadow: 0 2px 4px 2px rgba(143, 209, 233, .9);
}

.link:hover {
  background: #82d8e5 linear-gradient(#0147a6, #fff49f);
  transition: background-color 2s linear;
  box-shadow: 4px 4px 0 1px rgba(56, 100, 105, .5);
}

.star {
  width: 3px;
  height: 3px;
  opacity: 0;
  background-color: #fff;
  animation: blinkAnimation 1.5s infinite alternate;
  position: absolute;
}

@keyframes blinkAnimation {
  from {
    opacity: 0;
  }

  to {
    opacity: .5;
  }
}

